.c4l-all-purpose-card {

	&.is-style-compact-width .c4l-all-purpose-card-content {
		width: 75%;
		margin-left: auto;
		margin-right: auto;
	}

	&.is-style-full-width .c4l-all-purpose-card-content {
		width: 100%;
	}
}

.c4l-all-purpose-card-content {
	box-sizing: border-box;
	width: 75%;
	background-color: #f1f5fe;
	padding: 24px 36px 26px 36px;
	border: none;
	margin: 24px auto;
	position: relative;
	border-radius: 10px;

	p:last-of-type {
		margin-bottom: 0;
	}
}